.btn {
    display: inline-block;
    padding: 0.5em 2.5em;
    margin-top: 1em;
    margin-bottom: 5em;
    background: none;
    color: black;
    text-decoration: none;
    cursor: pointer;
    font-size: 14pt;
    transition: transform 200ms ease-in-out;
    text-transform: uppercase;
    border: 1px solid black;
    outline: none;
  }
  
.btn:hover {
    transform: scale(1.05);
  }

  #calculate {
    margin-top: 12em;
  }

